import type { FC } from "react";
import { useResumeContext } from "../../../context";
import type { Certificate, Education } from "../../../types/resume";
import { AWSIcon, MortarBoard } from "../../atoms";
import { FeatureArticle, Section } from "../../molecules";
import CertificateSubtitle from "./certificateSubtitle";
import EducationSubtitle from "./educationSubtitle";

interface IEducationCertificationSectionProps {
  educations: Education[];
  certificates: Certificate[];
}

const EducationCertificationSection: FC<
  IEducationCertificationSectionProps
> = ({ educations, certificates }) => {
  const { basics } = useResumeContext();

  return (
    <Section
      title="Education and Certification"
      uniqueId={"EDUCATION_SECTION"}
      className="flex-row mt-2"
    >
      {educations.map((educationObject, index, array) => {
        return (
          <FeatureArticle
            key={`${educationObject.institution}-${educationObject.studyType}-${educationObject.area}`}
            icon={<MortarBoard width={30} height={30} />}
            className="items-center print:w-1/2"
            setDetails={{ setSize: array.length, positionInSet: index + 1 }}
          >
            <EducationSubtitle education={educationObject} />
          </FeatureArticle>
        );
      })}
      {certificates.map((certificateObject, index, array) => {
        return (
          <FeatureArticle
            key={`${certificateObject.name}`}
            icon={<AWSIcon width={30} height={30} />}
            className="items-center"
            setDetails={{
              setSize: array.length + educations.length,
              positionInSet: index + educations.length + 1,
            }}
          >
            <CertificateSubtitle
              certificate={certificateObject}
              name={basics.name}
            />
          </FeatureArticle>
        );
      })}
    </Section>
  );
};

export default EducationCertificationSection;
