import type { FC } from "react";
import type { Education } from "../../../types/resume";
import { RenderYearMonth } from "../../../utils";

interface IEducationSubtitleProps {
  education: Education;
}

const EducationSubtitle: FC<IEducationSubtitleProps> = ({ education }) => {
  const graduatedDate = new Date(education.graduatedDate);

  return (
    <div>
      <p>
        {education.studyType} of {education.area}
      </p>
      <p>
        Graduated:{" "}
        <time dateTime={education.graduatedDate}>
          {RenderYearMonth(graduatedDate)}
        </time>
      </p>
    </div>
  );
};

export default EducationSubtitle;
