import type { FC, PropsWithChildren } from "react";
import { twMerge } from "tailwind-merge";
import { H2 } from "../../atoms";
import { Accordion } from "../accordion";

interface ISectionProps extends PropsWithChildren {
  title: string;
  uniqueId: string;
  className?: string;
}

const Section: FC<ISectionProps> = ({
  title,
  children,
  className,
  uniqueId,
}) => {
  return (
    <section
      className={twMerge(
        "print:w-full page:break-inside-avoid",
        "md:w-2/3 max-sm:w-full",
      )}
    >
      <Accordion uniqueId={uniqueId}>
        <div className="flex flex-row items-center">
          <H2>
            <Accordion.Trigger>{title}</Accordion.Trigger>
          </H2>
        </div>
        <Accordion.Content
          className={twMerge("print:w-full", "flex flex-col gap-8", className)}
        >
          {children}
        </Accordion.Content>
      </Accordion>
    </section>
  );
};

export default Section;
