import type { FC, PropsWithChildren, ReactNode } from "react";
import { twMerge } from "tailwind-merge";
import { H3 } from "../../atoms";
import { Accordion } from "../accordion";

interface ISkillSectionProps extends PropsWithChildren {
  title: ReactNode;
  subtitle?: ReactNode;
  uniqueId?: string;
  className?: string;
}

const SubSection: FC<ISkillSectionProps> = ({
  title,
  subtitle,
  children,
  uniqueId,
  className,
}) => {
  return (
    <section className="flex flex-col gap-4 w-full break-inside-avoid">
      <Accordion uniqueId={uniqueId}>
        <div className="flex flex-row">
          <H3 className="font-bold w-full">
            <Accordion.Trigger>
              <div
                className={twMerge(
                  "print:text-left",
                  "min-md:text-left text-center",
                )}
              >
                {title}
                {subtitle && <div className="font-semibold">{subtitle}</div>}
              </div>
            </Accordion.Trigger>
          </H3>
        </div>
        <Accordion.Content
          className={twMerge(
            "print:gap-4",
            "flex flex-row gap-8 grow justify-evenly w-full flex-wrap",
            className,
          )}
        >
          {children}
        </Accordion.Content>
      </Accordion>
    </section>
  );
};

export default SubSection;
