import { LinkedInLogoIcon } from "@radix-ui/react-icons";
import type { FC } from "react";
import type { Profile } from "../../../types/resume";
import { ExternalLink, GithubIcon } from "../../atoms";

interface IProfileBlockProps {
  profile: Profile;
  name: string;
}

const networkToIcon = (network: string) => {
  switch (network.toLowerCase()) {
    case "linkedin":
      return <LinkedInLogoIcon className="inline h-5 w-5" />;
    case "github":
      return <GithubIcon className="inline" />;
    default:
      throw new Error("Unrecognized Network");
  }
};

const ProfileBlock: FC<IProfileBlockProps> = ({ profile, name }) => {
  return (
    <ExternalLink
      label={`External link to ${name}'s ${profile.network}`}
      href={profile.url}
    >
      {networkToIcon(profile.network)}
      <span className="hidden print:inline print:ml-1">{profile.username}</span>
    </ExternalLink>
  );
};

export default ProfileBlock;
