import {
  createContext,
  type FC,
  type PropsWithChildren,
  useContext,
} from "react";
import type { ResumeSchema } from "../types/resume";

const ResumeContext = createContext<ResumeSchema | undefined>(undefined);

interface IResumeProvider extends PropsWithChildren {
  resume: ResumeSchema;
}

export const ResumeProvider: FC<IResumeProvider> = ({ resume, children }) => {
  return (
    <ResumeContext.Provider value={resume}>{children}</ResumeContext.Provider>
  );
};

export const useResumeContext = (): ResumeSchema => {
  const context = useContext(ResumeContext);

  if (!context) {
    throw new Error(
      "Usage of ResumeContext requires parent element with ResumeProvider",
    );
  }

  return context;
};
