import {
  ActivityLogIcon,
  EnvelopeClosedIcon,
  HandIcon,
  LapTimerIcon,
  LayersIcon,
  LightningBoltIcon,
  LinkBreak2Icon,
  MixIcon,
  Pencil2Icon,
  PersonIcon,
  ReaderIcon,
  RulerSquareIcon,
  Share1Icon,
} from "@radix-ui/react-icons";
import type { ReactNode } from "react";
import {
  AngularJsIcon,
  AWSIcon,
  BashIcon,
  BitbucketIcon,
  CIcon,
  CPlusPlusIcon,
  CSharpIcon,
  DJangoIcon,
  DockerIcon,
  DynamoDBIcon,
  GithubIcon,
  GitIcon,
  IonicIcon,
  JavaIcon,
  JenkinsIcon,
  LagomIcon,
  LinuxIcon,
  MongoDBIcon,
  MySQLIcon,
  NestJSIcon,
  PostgreSQLIcon,
  PythonIcon,
  ReactIcon,
  RedisIcon,
  SpringIcon,
  StrapiIcon,
  TerraformIcon,
  TerragruntIcon,
  TypescriptIcon,
} from "../components/atoms";

export function toIcon(name: string): ReactNode {
  switch (name.toLowerCase()) {
    //#region Programming Languages
    case "java":
      return <JavaIcon />;
    case "python":
      return <PythonIcon />;
    case "typescript":
      return <TypescriptIcon />;
    case "bash":
      return <BashIcon />;
    case "c#":
      return <CSharpIcon />;
    case "c":
      return <CIcon />;
    case "c++":
      return <CPlusPlusIcon />;
    case "c/c++":
      return (
        <>
          <CIcon width={10} height={10} />
          <CPlusPlusIcon width={10} height={10} />
        </>
      );
    //#endregion
    //#region Databases
    case "mysql":
      return <MySQLIcon />;
    case "redis":
      return <RedisIcon />;
    case "dynamodb":
      return <DynamoDBIcon />;
    case "postgresql":
      return <PostgreSQLIcon />;
    case "mongodb":
      return <MongoDBIcon />;
    //#endregion
    //#region Frameworks/Platforms/Libraries
    case "angularjs":
      return <AngularJsIcon />;
    case "django":
      return <DJangoIcon />;
    case "spring":
      return <SpringIcon />;
    case "nestjs":
      return <NestJSIcon />;
    case "react":
      return <ReactIcon />;
    case "lagom":
      return <LagomIcon />;
    case "ionic":
      return <IonicIcon />;
    case "strapi":
      return <StrapiIcon />;
    //#endregion
    //#region Devops
    case "bitbucket pipelines":
      return <BitbucketIcon />;
    case "github actions":
      return <GithubIcon />;
    case "aws":
      return <AWSIcon />;
    case "jenkins":
      return <JenkinsIcon />;
    case "terraform":
      return <TerraformIcon />;
    case "terragrunt":
      return <TerragruntIcon />;
    case "docker":
      return <DockerIcon />;
    //#endregion
    //#region Other
    case "git":
      return <GitIcon />;
    case "system design":
      return <Pencil2Icon className="mt-0.5" />;
    case "agile methodologies":
      return <LightningBoltIcon className="mt-0.5" />;
    case "stakeholder communication":
      return <EnvelopeClosedIcon className="mt-0.5" />;
    case "linux":
      return <LinuxIcon />;
    //#endregion
    //#region experience
    case "ruler":
      return <RulerSquareIcon className="mt-0.5" />;
    case "reader":
      return <ReaderIcon className="mt-0.5" />;
    case "mix":
      return <MixIcon className="mt-0.5" />;
    case "person":
      return <PersonIcon className="mt-0.5" />;
    case "share":
      return <Share1Icon className="mt-0.5" />;
    case "timer":
      return <LapTimerIcon className="mt-0.5" />;
    case "layers":
      return <LayersIcon className="mt-0.5" />;
    case "activity":
      return <ActivityLogIcon className="mt-0.5" />;
    case "break":
      return <LinkBreak2Icon className="mt-0.5" />;
    case "hand":
      return <HandIcon className="mt-0.5" />;
    //#endregion
    default:
      throw new Error(`No Icon for ${name}`);
  }
}
