import type { FC } from "react";
import { RenderYearMonth } from "../../../utils";
import { VerticalBar } from "../../atoms";

interface IExperienceSubtitleProps {
  name: string;
  startDate: string;
  endDate: string;
}

const ExperienceSubtitle: FC<IExperienceSubtitleProps> = ({
  name,
  startDate,
  endDate,
}) => {
  const start = new Date(startDate);
  const end = new Date(endDate);
  return (
    <div className="max-sm:flex max-sm:flex-col">
      {name} <VerticalBar className="max-sm:hidden" />{" "}
      <span className="italic font-extralight">
        <time dateTime={startDate}>{RenderYearMonth(start)}</time> -{" "}
        <time dateTime={endDate}>{RenderYearMonth(end)}</time>
      </span>
    </div>
  );
};

export default ExperienceSubtitle;
