import { type FC, Fragment, type ReactNode, useEffect, useState } from "react";
import { twMerge } from "tailwind-merge";
import type { Basics } from "../../../types/resume";
import { H1, H2, Text } from "../../atoms";
import ContactBlock from "../../molecules/contactBlock/contactBlock";

interface IHeroMainBlockProps {
  className?: string;
  basic: Basics;
  profileImage: ReactNode;
}

interface IPrivateContactDetails {
  mobile: string;
  email: string;
}

const MainBlock: FC<IHeroMainBlockProps> = ({
  className,
  basic,
  profileImage,
}) => {
  const [privateContactDetails, setPrivateContactDetails] = useState<
    IPrivateContactDetails | undefined
  >();

  useEffect(() => {
    if (process.env.GATSBY_MOBILE && process.env.GATSBY_EMAIL) {
      setPrivateContactDetails({
        mobile: process.env.GATSBY_MOBILE,
        email: process.env.GATSBY_EMAIL,
      });
    }
  }, []);

  const heroText = basic.summary.split("\n");

  const text = Array.isArray(heroText) ? (
    heroText.map((text) => (
      <Fragment key={`heroText-${text}`}>
        <Text>{text}</Text>
      </Fragment>
    ))
  ) : (
    <Text>{heroText}</Text>
  );

  return (
    <div
      className={twMerge(
        "print:w-full print:gap-6",
        "flex flex-col items-start gap-8",
        className,
      )}
    >
      <div
        className={twMerge(
          "print:gap-4 print:justify-between print:w-full print:items-center",
          "flex flex-row",
        )}
      >
        <div>
          <H1>{basic.name}</H1>
          <H2 className="mt-4 print:mt-1">{basic.label}</H2>
        </div>
        <div className="hidden print:flex flex-row- justify-between items-center gap-4">
          <div className="max-w-16">{profileImage}</div>
          <ContactBlock profiles={basic.profiles} {...privateContactDetails} />
        </div>
      </div>
      {text}
    </div>
  );
};

export default MainBlock;
