import { DotFilledIcon } from "@radix-ui/react-icons";
import type { FC } from "react";
import type { Work } from "../../../types/resume";
import { toIcon } from "../../../utils";
import { Text } from "../../atoms";
import { FeatureArticle, Section, SubSection } from "../../molecules";
import ExperienceSubtitle from "./experienceSubtitle";

interface IExperienceSectionProps {
  works: Work[];
}

const _STORAGE_KEY = "EXPERIENCE_SECTION";

const ExperienceSection: FC<IExperienceSectionProps> = ({ works }) => {
  return (
    <Section title="Experience" uniqueId={_STORAGE_KEY} className="print:gap-4">
      {works.map((workExperience) => {
        const key = `${workExperience.name}_${workExperience.startDate}_${workExperience.endDate}`;
        return (
          <SubSection
            key={key}
            title={workExperience.position}
            subtitle={
              <ExperienceSubtitle
                name={workExperience.name}
                startDate={workExperience.startDate}
                endDate={workExperience.endDate}
              />
            }
            uniqueId={`${_STORAGE_KEY}_${key.toUpperCase()}`}
            className="print:justify-between print:gap-0"
          >
            {workExperience.duties.map((duty, index, array) => (
              <FeatureArticle
                key={duty.description}
                icon={
                  <div>
                    <span className="print:hidden">{toIcon(duty.icon)}</span>
                    <span className="hidden print:inline">
                      <DotFilledIcon />
                    </span>
                  </div>
                }
                setDetails={{ setSize: array.length, positionInSet: index + 1 }}
              >
                <Text>{duty.description}</Text>
              </FeatureArticle>
            ))}
          </SubSection>
        );
      })}
    </Section>
  );
};

export default ExperienceSection;
