import type { FC, PropsWithChildren, ReactNode } from "react";
import { twMerge } from "tailwind-merge";
import type { ISetDetails } from "../../../types";
import { H4 } from "../../atoms";

interface IFeatureArticleProps extends PropsWithChildren {
  icon: ReactNode;
  title?: ReactNode;
  className?: string;
  setDetails?: ISetDetails;
}

const FeatureArticle: FC<IFeatureArticleProps> = ({
  icon,
  title,
  children,
  className,
  setDetails,
}) => {
  return (
    <article
      className={twMerge("print:gap-1", "flex flex-row gap-2", className)}
      aria-setsize={setDetails?.setSize}
      aria-posinset={setDetails?.positionInSet}
    >
      {icon && <div className="py-1">{icon}</div>}
      <div className="flex flex-col">
        {title && <H4>{title}</H4>}
        <div>{children}</div>
      </div>
    </article>
  );
};

export default FeatureArticle;
