import { EnvelopeClosedIcon, MobileIcon } from "@radix-ui/react-icons";
import type { FC } from "react";
import { twMerge } from "tailwind-merge";
import { useResumeContext } from "../../../context";
import type { Profile } from "../../../types/resume";
import ProfileBlock from "./profileBlock";

interface IContactBlockProps {
  mobile?: string;
  email?: string;
  profiles: Profile[];
}

const ContactBlock: FC<IContactBlockProps> = ({ profiles, mobile, email }) => {
  const { basics } = useResumeContext();
  return (
    <address
      className={twMerge(
        "print:flex-col print:gap-0 print:pt-0",
        "pt-4 flex flex-row gap-2",
      )}
    >
      {mobile && (
        <a href={`tel:${mobile}`}>
          <MobileIcon className="inline mr-1 h-5 w-5" />
          {mobile}
        </a>
      )}
      {email && (
        <a href={`mailto:${email}`}>
          <EnvelopeClosedIcon className="inline mr-1 h-5 w-5" />
          {email}
        </a>
      )}
      {profiles.map((profile) => (
        <ProfileBlock
          key={profile.network}
          profile={profile}
          name={basics.name}
        />
      ))}
    </address>
  );
};

export default ContactBlock;
