import { ChevronRightIcon } from "@radix-ui/react-icons";
import {
  createContext,
  type FC,
  type PropsWithChildren,
  useCallback,
  useContext,
  useEffect,
  useState,
} from "react";
import { twMerge } from "tailwind-merge";
import { getValueBoolean, setValueBoolean } from "../../../utils";

interface IAccordionContext {
  isOpen: boolean;
  setIsOpen: (state: boolean) => void;
  uniqueId?: string;
}

const AccordionContext = createContext<IAccordionContext | undefined>(
  undefined,
);

const useAccordionContext = (): IAccordionContext => {
  const context = useContext(AccordionContext);

  if (!context) {
    throw new Error("Accordion Context is required");
  }

  return context;
};

interface IAccordionContentProps extends PropsWithChildren {
  className?: string;
}

interface IAccordionProps extends PropsWithChildren {
  uniqueId?: string;
}

interface IAccordion extends FC<IAccordionProps> {
  Trigger: FC<PropsWithChildren>;
  Content: FC<IAccordionContentProps>;
}

const sanitizeId = (id: string) => id.replaceAll(" ", "_");

const Accordion: IAccordion = ({ children, uniqueId }) => {
  const [isOpen, setIsOpen] = useState<boolean>(true);
  const sanitizedId = uniqueId && sanitizeId(uniqueId);
  useEffect(() => {
    if (sanitizedId) {
      setIsOpen(getValueBoolean(sanitizedId));
    }
  }, [sanitizedId]);

  const setState = useCallback(
    (state: boolean) => {
      setIsOpen(state);

      if (sanitizedId) {
        setValueBoolean(sanitizedId, state);
      }
    },
    [sanitizedId],
  );

  return (
    <AccordionContext.Provider
      value={{ isOpen, setIsOpen: setState, uniqueId: sanitizedId }}
    >
      {children}
    </AccordionContext.Provider>
  );
};

Accordion.Trigger = ({ children }) => {
  const { isOpen, setIsOpen, uniqueId } = useAccordionContext();

  const onClick = useCallback(() => setIsOpen(!isOpen), [isOpen, setIsOpen]);

  if (!uniqueId) {
    return children;
  }

  return (
    <button
      id={`${uniqueId}-button`}
      onClick={onClick}
      type="button"
      aria-expanded={isOpen}
      aria-controls={`${uniqueId}-content`}
      className="flex flex-row"
    >
      <ChevronRightIcon
        className={twMerge(
          "print:hidden",
          "h-12 w-12 text-slate-700 transition-all duration-500 ease-in-out",
          isOpen && "rotate-90",
        )}
      />
      {children}
    </button>
  );
};

Accordion.Content = ({ children, className }) => {
  const { isOpen, uniqueId } = useAccordionContext();
  return (
    <div
      id={uniqueId && `${uniqueId}-content`}
      className={twMerge(
        "print:max-h-[9999px]",
        "transition-max-height duration-500 ease-in-out overflow-clip",
        isOpen ? "max-h-[9999px]" : "max-h-[0px]",
        className,
      )}
      aria-hidden={!isOpen}
    >
      {children}
    </div>
  );
};
export default Accordion;
