import { type FC, useMemo } from "react";
import type { Skill } from "../../../types/resume";
import { toIcon } from "../../../utils";
import { FeatureArticle, Section, SubSection } from "../../molecules";

interface ISkillSectionProps {
  skills: Skill[];
}

const _STORAGE_KEY = "SKILL_SECTION";

const skillSection: FC<ISkillSectionProps> = ({ skills }) => {
  const subSections = useMemo(
    () =>
      skills.reduce(
        (acc, skill) => {
          const groupKey = String(skill.category);

          if (!acc[groupKey]) {
            acc[groupKey] = [];
          }

          acc[groupKey].push(skill);

          // Return the accumulator for the next iteration
          return acc;
        },
        {} as Record<string, Skill[]>,
      ),
    [skills],
  );

  return (
    <Section title="Skills" uniqueId={_STORAGE_KEY}>
      {Object.keys(subSections).map((category) => {
        return (
          <SubSection key={category} title={category}>
            {subSections[category].map((skill, index, array) => (
              <FeatureArticle
                key={skill.name}
                title={skill.name}
                icon={toIcon(skill.name)}
                setDetails={{ setSize: array.length, positionInSet: index + 1 }}
              />
            ))}
          </SubSection>
        );
      })}
    </Section>
  );
};

export default skillSection;
