import type { FC } from "react";
import type { Certificate } from "../../../types/resume";
import { RenderYearMonth } from "../../../utils";
import { ExternalLink } from "../../atoms";

interface ICertificateSubtitleProps {
  certificate: Certificate;
  name: string;
}

const CertificateSubtitle: FC<ICertificateSubtitleProps> = ({
  certificate,
  name,
}) => {
  const issuedDate = new Date(certificate.issued);

  return (
    <div>
      {certificate.url ? (
        <ExternalLink
          label={`External link to ${name}'s ${certificate.name}`}
          href={certificate.url}
        >
          {certificate.name}
        </ExternalLink>
      ) : (
        <p> {certificate.name}</p>
      )}
      <p>
        Issued:{" "}
        <time dateTime={certificate.issued}>{RenderYearMonth(issuedDate)}</time>
      </p>
    </div>
  );
};

export default CertificateSubtitle;
